<template>
  <v-container>
    <v-progress-linear
      :active="loadingJobs"
      :indeterminate="loadingJobs"
      absolute
      top
      color="primary accent-4"
    ></v-progress-linear>
    <v-row justify="center">
      <v-expansion-panels focusable>
        <v-expansion-panel v-for="job in jobsOnPage" :key="job.JobId">
          <v-expansion-panel-header @click="getCurrentJob(job.JobId)">
            <v-row
              :class="
                $vuetify.breakpoint.smAndDown
                  ? 'd-flex flex-column'
                  : 'd-flex flex-row justify-space-between'
              "
            >
              <h3>{{ job.JobTitle.trim() }}</h3>
              <span :class="$vuetify.breakpoint.smAndDown ? 'pt-4' : 'px-8'">
                {{ getDate(job.UpdateDate) }}
              </span>
            </v-row>
          </v-expansion-panel-header>
          <v-expansion-panel-content>
            <section
              v-html="currentJob.Description"
              class="d-flex flex-column px-4 pt-4"
            ></section>
            <v-progress-linear
              :active="loading"
              :indeterminate="loading"
              absolute
              bottom
              color="primary accent-4"
            ></v-progress-linear>
            <v-col v-if="!loading" cols="12" class="d-flex justify-end">
              <v-btn small ripple @click="showApply">
                {{ $vuetify.lang.t('$vuetify.iai.apply.applyFreind') }}
              </v-btn>
            </v-col>
          </v-expansion-panel-content>
        </v-expansion-panel>
        <v-pagination
          :length="+length"
          :total-visible="7"
          v-model="page"
        ></v-pagination>
      </v-expansion-panels>
    </v-row>
  </v-container>
</template>

<script>
import axios from 'axios';

export default {
  name: 'JobList',

  data: () => ({
    page: 1,
    perPage: 10,
    loading: true,
    loadingJobs: true,
    currentJob: {},
    jobs: []
  }),
  mounted() {
    this.loadingJobs = true;
    axios.get('/api/iai/jobs').then(response => {
      this.loadingJobs = false;
      this.jobs = response.data;
    });
  },
  computed: {
    length() {
      return this.jobs.length
        ? (+this.jobs.length / this.perPage).toFixed(0)
        : 0;
    },
    jobsOnPage() {
      return this.jobs.slice(
        (this.page - 1) * this.perPage,
        (this.page - 1) * this.perPage + this.perPage
      );
    }
  },
  methods: {
    getDate(dateString) {
      const date = new Date(dateString);
      return `${date.getDate()}/${date.getMonth() + 1}/${date.getFullYear()}`;
    },
    getCurrentJob(id) {
      this.loading = true;
      this.currentJob.Description = '';
      axios.get(`/api/iai/job/${id}`).then(response => {
        this.loading = false;
        this.currentJob = response.data;
      });
    },
    showApply() {
      this.$emit('showApply');
    }
  }
};
</script>
