<template>
  <v-row justify="center">
    <v-dialog v-model="dialog" max-width="600px">
      <v-card>
        <v-card-title>
          <v-progress-linear
            :active="loading"
            :indeterminate="loading"
            absolute
            bottom
            color="primary accent-4"
          ></v-progress-linear>
          <span class="headline font-weight-bold">{{
            $vuetify.lang.t('$vuetify.iai.apply.title')
          }}</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form ref="form" v-model="valid" lazy-validation>
              <h3>
                {{ $vuetify.lang.t('$vuetify.iai.apply.applicantDetails') }}
              </h3>
              <v-row>
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model.trim.lazy="applicantName"
                    :rules="[
                      v =>
                        !!v ||
                        $vuetify.lang.t('$vuetify.iai.rules.nameIsRequired')
                    ]"
                    prepend-icon="mdi-account"
                    :label="
                      $vuetify.lang.t('$vuetify.iai.apply.applicantFullName') +
                        '*'
                    "
                    required
                  ></v-text-field>
                </v-col>
              </v-row>
              <v-row>
                <v-col cols="12" sm="6">
                  <v-file-input
                    v-model="cvFile"
                    accept=".doc, .docx, application/msword, application/vnd.openxmlformats-officedocument.wordprocessingml.document, .pdf"
                    :label="$vuetify.lang.t('$vuetify.iai.apply.cvFile')"
                    value
                  ></v-file-input>
                </v-col>
              </v-row>
              <h3>
                {{ $vuetify.lang.t('$vuetify.iai.apply.employeeDetails') }}
              </h3>
              <v-row>
                <v-col cols="12" sm="6">
                  <v-text-field
                    v-model.trim.lazy="employeeName"
                    :rules="[
                      v =>
                        !!v ||
                        $vuetify.lang.t('$vuetify.iai.rules.nameIsRequired')
                    ]"
                    prepend-icon="mdi-account-hard-hat"
                    :label="
                      $vuetify.lang.t('$vuetify.iai.apply.employeeFullName') +
                        '*'
                    "
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6">
                  <v-text-field
                    :class="$vuetify.rtl ? 'text-ltr' : ''"
                    v-model.trim.lazy="employeePhone"
                    :rules="phoneRules"
                    prepend-icon="mdi-phone"
                    :label="$vuetify.lang.t('$vuetify.iai.apply.phone') + '*'"
                    required
                  ></v-text-field>
                </v-col>
                <v-col cols="12">
                  <v-text-field
                    :class="$vuetify.rtl ? 'text-ltr' : ''"
                    v-model.trim.lazy="employeeEmail"
                    :rules="emailRules"
                    prepend-icon="mdi-email"
                    :label="$vuetify.lang.t('$vuetify.iai.apply.email') + '*'"
                    required
                  ></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
          <small>{{ $vuetify.lang.t('$vuetify.requiredFields') }}</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-1" text @click="dialog = false">
            {{ $vuetify.lang.t('$vuetify.close') }}
          </v-btn>
          <v-btn color="blue darken-1" text @click="applyFreind">
            {{ $vuetify.lang.t('$vuetify.save') }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-snackbar v-model="snackbar">
      {{ snackbarText }}
      <v-btn color="pink" text @click="snackbar = false">
        {{ $vuetify.lang.t('$vuetify.close') }}
      </v-btn>
    </v-snackbar>
  </v-row>
</template>

<script>
import axios from 'axios';
export default {
  data: () => ({
    loading: false,
    dialog: false,
    currentJob: {},
    valid: false,
    applicantName: '',
    cvFile: null,
    employeeName: '',
    employeeEmail: '',
    employeePhone: '',
    emailRules: [],
    phoneRules: [],
    snackbar: false,
    snackbarText: ''
  }),
  methods: {
    showApply(currentJob) {
      this.currentJob = currentJob;
      this.dialog = true;
    },
    applyData() {
      let applyData = new FormData();

      applyData.append('applicantName', this.applicantName);
      applyData.append('cvFile', this.cvFile);
      applyData.append('employeeName', this.employeeName);
      applyData.append('employeeEmail', this.employeeEmail);
      applyData.append('employeePhone', this.employeePhone);
      applyData.append('jobId', this.currentJob.JobId);
      applyData.append('jobCode', this.currentJob.JobCode);
      applyData.append(
        'jobTitle',
        this.currentJob.JobTitle ? this.currentJob.JobTitle.trim() : ''
      );

      return applyData;
    },
    applyFreind() {
      this.loading = true;
      axios
        .post('/api/iai/apply', this.applyData())
        .then(response => {
          this.loading = false;
          this.dialog = false;
          this.jobs = response.data;
          this.snackbar = true;
          this.snackbarText = this.$vuetify.lang.t(
            '$vuetify.iai.apply.appliedSuccessfuly'
          );
        })
        .catch(function(error) {
          this.loading = false;
          this.dialog = false;
          this.snackbar = true;
          this.snackbarText = this.$vuetify.lang.t(
            '$vuetify.iai.apply.appliedError'
          );
          // eslint-disable-next-line
          console.log(error);
        });
    }
  },
  created() {
    this.phoneRules = [
      v => !!v || this.$vuetify.lang.t('$vuetify.iai.rules.phoneIsRequired'),
      v =>
        /^0[0-9]{1,2}[-\s]{0,1}[0-9]{3}[-\s]{0,1}[0-9]{4}/.test(v) ||
        this.$vuetify.lang.t('$vuetify.iai.rules.illegalPhone')
    ];
    this.emailRules = [
      v => !!v || this.$vuetify.lang.t('$vuetify.iai.rules.emailIsRequired'),
      v =>
        /.+@.+\..+/.test(v) ||
        this.$vuetify.lang.t('$vuetify.iai.rules.illegalEmail')
    ];
  }
};
</script>

<style lang="css">
.text-ltr input {
  direction: ltr !important;
  text-align: right;
}
</style>
