<template>
  <v-app>
    <v-app-bar app color="primary" dark>
      <div class="d-flex align-center">
        <v-img
          alt="IAI Logo"
          class="shrink mr-2"
          contain
          src="./assets/logo-white.png"
          transition="scale-transition"
          width="40"
        />
      </div>

      <v-spacer></v-spacer>
      <v-btn small ripple rounded color="info" @click="toggleLang">
        {{ lang }}
      </v-btn>
      <v-btn href="https://www.iai.co.il" target="_blank" text>
        <span class="mr-2">{{ $vuetify.lang.t('$vuetify.iai.webSite') }}</span>
        <v-icon>mdi-open-in-new</v-icon>
      </v-btn>
    </v-app-bar>

    <v-content>
      <JobList v-on:showApply="showApply" ref="jobList" />
      <Apply ref="apply" />
    </v-content>
  </v-app>
</template>

<script>
import JobList from './components/JobList';
import Apply from './components/Apply';

export default {
  name: 'App',

  components: {
    JobList,
    Apply
  },

  data: () => ({
    lang: ''
  }),
  methods: {
    toggleLang() {
      this.lang = this.lang === 'he' ? 'en' : 'he';
    },
    showApply() {
      // Call the Apply method by ref
      this.$refs.apply.showApply(this.$refs.jobList.currentJob);
    }
  },
  created() {
    this.lang = this.$vuetify.lang.current;
  },
  watch: {
    lang() {
      if (this.lang === 'he') {
        this.$vuetify.lang.current = 'en';
        this.$vuetify.rtl = false;
      } else {
        this.$vuetify.lang.current = 'he';
        this.$vuetify.rtl = true;
      }
    }
  }
};
</script>
